<template>
  <div>
    <el-card class="box-card">
      <el-col :span="24">
        <el-row>
          <el-col :span="24">
            <el-form :inline="true" class="demo-form-inline">
              <el-form-item label="搜索">
                <el-input v-model="QueryWhere" clearable placeholder="根据账号/管理员查询"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="onSubmit">查询</el-button>
              </el-form-item>
              <el-form-item>
                <el-button type="success" @click="addUser" icon="el-icon-circle-plus-outline">添加用户</el-button>
              </el-form-item>
            </el-form>
          </el-col>
          <el-col :span="24" style="margin-top:10px;">
            <el-table :row-class-name="tableRowClassName" :data="tableData" border onselectstart="return false">
              <el-table-column fixed width="50" prop="ID" label="序号" align="center"></el-table-column>
              <el-table-column fixed width="200" prop="UserName" label="账号" align="center"></el-table-column>
              <el-table-column width="100" prop="RoleID" label="角色ID" align="center"></el-table-column>
              <el-table-column width="200" prop="LastLoginTime" label="上次登录日期" align="center"></el-table-column>
              <el-table-column width="200" prop="uploadDh" label="设备数" align="center"></el-table-column>
              <el-table-column width="200" prop="Name" label="管理员" align="center"></el-table-column>
              <el-table-column width="200" prop="uploadState" label="联系方式" align="center"></el-table-column>
              <el-table-column width="150" label="是否警员" align="center">
                <template slot-scope="scope">
                  <el-checkbox :v-model="scope.row.PoliceFlag"></el-checkbox>
                </template>
              </el-table-column>
              <el-table-column width="200" prop="ip" label="IP" align="center"></el-table-column>
              <el-table-column width="150" fixed="right" label="详情" align="center">
                <template slot-scope="scope">
                  <el-tooltip content="管理" placement="top">
                    <el-button @click="openVisible(scope.$index, scope.row)" type="primary" icon="el-icon-eleme"
                      circle></el-button>
                  </el-tooltip>
                  <el-button v-if="scope.$index == 1" type="text" size="small">解除锁定</el-button>
                </template>
              </el-table-column>
            </el-table>
            <el-pagination
              background
              layout="prev, pager, next ,sizes"
              :total="TotalCount"
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              style="margin-top:10px;"
            ></el-pagination>
          </el-col>
        </el-row>
      </el-col>
    </el-card>
    <!-- 账号管理 -->
    <el-dialog title="添加用户" width="40%" :visible.sync="dialogVisible1">
      <el-form label-width="100px">
        <el-col class="dialog">
          <el-form-item label="账号">
            <el-input v-model="formInline1.value1" placeholder="账号"></el-input>
          </el-form-item>
          <el-form-item label="管理员">
            <el-input v-model="formInline1.value2" placeholder="管理员"></el-input>
          </el-form-item>
          <el-form-item label="角色ID">
            <el-select v-model="formInline.value" clearable placeholder="选择角色ID">
                <el-option label="1" value="1"></el-option>
                <el-option label="2" value="2"></el-option>
                <el-option label="3" value="3"></el-option>
                <el-option label="4" value="4"></el-option>
            </el-select>
        </el-form-item>
        </el-col>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible1 = false">取 消</el-button>
        <el-button type="success" @click="upAjax1">添 加</el-button>
      </div>
    </el-dialog>
    <!-- 设备管理 -->
    <el-dialog title="设备绑定" width="50%" :visible.sync="dialogVisible">
        <el-transfer
            v-model="yesData"
            :props="{key: 'id',label: 'name'}"
            :titles="['未关联', '已关联']"
            @change="handleChange"
            :data="noData"
        ></el-transfer>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="upAjax">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  const axios = require("axios");
  import Qs from 'qs';
  export default {
    data() {
      return{
        formInline1:{
          value1:'',
          value2:'',
          value3:''
        },
        formInline:{
          value1:'',
          value2:''
        },
        noData:'',
        yesData:'',
        tableData:[],
        SeeClickData:'',
        dialogVisible:false,
        dialogVisible1:false,
        Page:1,
        PageSize:10,
        QueryWhere:'',
        UserName:'',
        SessionKey:'',
        TotalCount:''
      }
    },
    methods:{
       tableRowClassName({row, rowIndex}) {
        if (rowIndex === 1) {
          return 'warning-row';
        }
        return '';
      },
      onSubmit(){
        this.getData()
      },
      handleSizeChange(val) {
        this.PageSize = val
        this.getData()
      },
      handleCurrentChange(val) {
        this.Page = val
        this.getData()
      },
      openVisible(tab, event) {
        this.SeeClickData = event
        this.dialogVisible = true
        console.log(this.SeeClickData)
        // this.$confirm('您将激活用户 gao_970707 并延长有效期, 是否继续?', '提示', {
        //   confirmButtonText: '确定',
        //   cancelButtonText: '取消',
        //   type: 'warning'
        // }).then(() => {
        //   this.$message({
        //     type: 'success',
        //     message: '删除成功!'
        //   });
        // }).catch(() => {
        //   this.$message({
        //     type: 'info',
        //     message: '已取消删除'
        //   });          
        // });
      },
      //添加账号
      addUser(){
        this.dialogVisible1 = true
      },
      upAjax1(){
        console.log('添加')
        this.dialogVisible1 = false
      },
      upAjax(){
        console.log('保存')
        this.dialogVisible = false
      },
      handleChange(value, direction, movedKeys) {
        console.log(value);
      },
      getData(){
          axios({
            method: "post",
            url: http+"api/ExamineWeb/GetUserList",
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
            },
            data: Qs.stringify({
                Page: this.Page,
                PageSize: this.PageSize,
                QueryWhere:this.QueryWhere,
                UserName:this.UserName,
                SessionKey:this.SessionKey
            })
        })
        .then(res => {
            console.log(res);
            if (res.data.IsSuccess) {
                this.TotalCount = res.data.TotalCount
                this.tableData = res.data.Data
            }
        })
        .catch(error => {
            this.$message({
                showClose: true,
                message: "服务器错误",
                type: "error"
            });
        });
      }
    },
    mounted(){
        this.UserName = sessionStorage.getItem("UserName")
        this.SessionKey = sessionStorage.getItem("SessionKey")
        this.getData()
    }
  }
</script>
<style scoped>
  .box-card{
    padding: 20px;
  }
  .el-icon-success {
    color: yellowgreen;
    font-size: 20px;
  }

  .el-icon-error {
    color: red;
    font-size: 20px;
  }
  .dialog{
    padding: 20px;
  }
  .el-table .warning-row {
    background: oldlace;
  }
</style>